@charset "UTF-8";
/*
  @component rich-text
  @display RichText
  @chinese 富文本
  @family data-display
*/
:root {
  /*
    @desc padding-lr
    @semantic 左右内边距
    @namespace size/common
  */
  --richtext-padding-lr: var(--s-2);
  /*
    @desc padding-tb
    @semantic 上下内边距
    @namespace size/common
  */
  --richtext-padding-tb: var(--s-2);
  /*
    @desc link-padding-lr
    @semantic 链接左右内边距
    @namespace size/link
  */
  --richtext-link-padding-tb: var(--s-1);
  /*
    @desc table-border-width
    @semantic 表格边框粗细~
    @namespace size/table
  */
  --richtext-table-border-width: var(--line-1);
  /*
    @desc font-color
    @semantic 正文字体色
    @namespace style/font
  */
  --richtext-normal-font-color: var(--color-text1-4);
  /*
    @desc link-color
    @semantic 链接色
    @namespace style/link
  */
  --richtext-link-color: var(--color-link-3);
  /*
    @desc table-border-color
    @semantic 表格边框色
    @namespace style/table
  */
  --richtext-table-border-color: var(--color-line1-3);
}

.mt-rich-text {
  padding: var(--richtext-padding-tb) var(--richtext-padding-lr);
  color: var(--richtext-normal-font-color);
  overflow-x: auto;
  overflow-y: hidden;
  -webkit-overflow-scrolling: touch;
}
.mt-rich-text ._select {
  -webkit-user-select: text;
  user-select: text;
}
.mt-rich-text ._a {
  display: inline-block;
  padding: 0 var(--richtext-link-padding-tb);
  color: var(--richtext-link-color);
  word-break: break-all;
}
.mt-rich-text ._a:active, .mt-rich-text ._a:hover {
  text-decoration: underline;
  opacity: 0.7;
}
.mt-rich-text ._img {
  max-width: 100%;
  -webkit-touch-callout: none;
}
.mt-rich-text ._b,
.mt-rich-text ._strong {
  font-weight: 700;
}
.mt-rich-text ._code {
  font-family: monospace;
}
.mt-rich-text ._del {
  text-decoration: line-through;
}
.mt-rich-text ._em,
.mt-rich-text ._i {
  font-style: italic;
}
.mt-rich-text ._h1 {
  font-size: 2em;
}
.mt-rich-text ._h2 {
  font-size: 1.5em;
}
.mt-rich-text ._h3 {
  font-size: 1.17em;
}
.mt-rich-text ._h5 {
  font-size: 0.83em;
}
.mt-rich-text ._h6 {
  font-size: 0.67em;
}
.mt-rich-text ._h1,
.mt-rich-text ._h2,
.mt-rich-text ._h3,
.mt-rich-text ._h4,
.mt-rich-text ._h5,
.mt-rich-text ._h6 {
  display: block;
  font-weight: 700;
  margin: 0.5em 0;
}
.mt-rich-text ._ins {
  text-decoration: underline;
}
.mt-rich-text ._li {
  display: list-item;
}
.mt-rich-text ._ol {
  list-style-type: decimal;
}
.mt-rich-text ._ol,
.mt-rich-text ._ul {
  display: block;
  padding-left: 10.666666666666666vw;
  margin: 1em 0;
}
.mt-rich-text ._q::before {
  content: '"';
}
.mt-rich-text ._q::after {
  content: '"';
}
.mt-rich-text ._sub {
  font-size: smaller;
  vertical-align: sub;
}
.mt-rich-text ._sup {
  font-size: smaller;
  vertical-align: super;
}
.mt-rich-text ._table {
  display: table;
  border-collapse: collapse;
  border-top: var(--richtext-table-border-width) solid var(--richtext-table-border-color);
  border-left: var(--richtext-table-border-width) solid var(--richtext-table-border-color);
}
.mt-rich-text ._tbody,
.mt-rich-text ._tfoot,
.mt-rich-text ._thead {
  display: table-row-group;
}
.mt-rich-text ._tr {
  display: table-row;
}
.mt-rich-text ._td,
.mt-rich-text ._th {
  display: table-cell;
  vertical-align: middle;
  border-right: var(--richtext-table-border-width) solid var(--richtext-table-border-color);
  border-bottom: var(--richtext-table-border-width) solid var(--richtext-table-border-color);
  overflow: hidden;
}
.mt-rich-text ._th {
  font-weight: 700;
  text-align: center;
}
.mt-rich-text ._ul {
  list-style-type: disc;
}
.mt-rich-text ._ul ._ul {
  margin: 0;
  list-style-type: circle;
}
.mt-rich-text ._ul ._ul ._ul {
  list-style-type: square;
}
.mt-rich-text ._abbr,
.mt-rich-text ._b,
.mt-rich-text ._code,
.mt-rich-text ._del,
.mt-rich-text ._em,
.mt-rich-text ._i,
.mt-rich-text ._ins,
.mt-rich-text ._label,
.mt-rich-text ._q,
.mt-rich-text ._span,
.mt-rich-text ._strong,
.mt-rich-text ._sub,
.mt-rich-text ._sup {
  display: inline;
}
.mt-rich-text ._blockquote,
.mt-rich-text ._div,
.mt-rich-text ._p {
  display: block;
}
.mt-rich-text ._p {
  margin-bottom: 0.5em;
}
.mt-rich-text ._video {
  width: 100%;
}
.mt-rich-text ._img-error {
  width: 26.666666666666668vw;
  height: 26.666666666666668vw;
}
.mt-rich-text table {
  display: table;
  border-collapse: collapse;
  border: var(--richtext-table-border-width) solid var(--richtext-table-border-color);
}
.mt-rich-text th,
.mt-rich-text td {
  border: var(--richtext-table-border-width) solid var(--richtext-table-border-color);
}
.mt-rich-text a {
  color: var(--richtext-link-color);
  padding: 0 var(--richtext-link-padding-tb);
}
.mt-rich-text a:active, .mt-rich-text a:hover {
  color: var(--richtext-link-color);
  text-decoration: underline;
  opacity: 0.7;
}