  @unconfigurable
  @component motion
  @style
*/
:root {
  /*
   @desc motion-1
   @group motion.time
   @semantic 动画时间
   */
  --motion-time-1: 0.1s;
  /*
   @desc motion-2
   @group motion.time
   @semantic 动画时间
   */
  --motion-time-2: 0.25s;
  /*
   @desc motion-3
   @group motion.time
   @semantic 动画时间
   */
  --motion-time-3: 0.3s;
  /*
   @desc motion-linear
   @group motion.type
   @semantic 动画时间
   */
  --motion-type-linear: cubic-bezier(0, 0, 1, 1);
  /*
   @desc motion-ease
   @group motion.type
   @semantic 动画时间
   */
  --motion-type-ease: ease;
  /*
   @desc motion-ease-in
   @group motion.type
   @semantic 动画时间
   */
  --motion-type-ease-in: ease-in;
  /*
   @desc motion-ease-out
   @group motion.type
   @semantic 动画时间
   */
  --motion-type-ease-out: ease-out;
  /*
   @desc motion-ease-in-out-back
   @group motion.type
   @semantic 动画时间
   */
  --motion-type-ease-in-out-back: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  /*
   @desc motion-ease-in-out-back
   @group motion.type
   @semantic 动画时间
   */
  --motion-type-ease-in-out: ease-in-out;
}

