@charset "UTF-8";
/*
  @component calendar
  @display Calendar
  @chinese 日历
  @family data-display
*/
:root {
  /*
    @desc border-color
    @semantic 头部分割线颜色(`card/fullscreen`模式)
    @namespace state/head/select
