    @desc icon-color
    @semantic 头部图标颜色(`panel/range`模式)
    @namespace state/normal/head
  */
  --calendar-header-normal-icon-color: var(--color-text1-2);
  /*
    @desc disable-icon-color
    @semantic 头部图标禁用颜色
    @namespace state/normal/head
  */
  --calendar-header-normal-icon-disabled-color: var(--color-text1-1);
