    @desc padding(t,b)
    @semantic 上下内边距
    @namespace size/content
  */
  --calendar-content-padding-vertical: var(--s-5);
  /*
    @desc padding(l,r)
    @semantic 左右内边距
    @namespace size/content
  */
  --calendar-content-padding-horizon: var(--s-3);
  /*
    @desc margin-bottom
    @semantic 内容下边距(`month`模式)
